
body {
    padding-bottom: 60px;
}


.team {
    &.red   {&, & a {color: @red}}
    &.blue  {&, & a {color: @blue}}
    &.green {&, & a {color: @green}}
}

.team-bg {
    &.red   {background-color: @red-bg}
    &.blue  {background-color: @blue-bg}
    &.green {background-color: @green-bg}
}